// =============================================================================
// > ADMIN STYLES
// =============================================================================

@import "./assets/variables";
@import "./assets/utils";

// ==================================================
// > THEME
// ==================================================
.admin-color-fresh,
.logged-in.admin-bar {
    #wp-admin-bar-site-name > a:before {
        content: "" !important;
        height: 32px;
        width: 32px;
        margin: -2px 5px 0 0;
        padding: 0;
        background: img("logo.png") center center / contain no-repeat !important; }

    #wp-admin-bar-wp-logo,
    #wp-admin-bar-comments,
    #wp-admin-bar-updates,
    #wp-admin-bar-new-post,
    .update-plugins {
        display: none !important; }

    #adminmenu,
    #adminmenuback,
    #adminmenuwrap {
        background: #333; }

    #adminmenu .wp-submenu {
        background: #444; }

    #adminmenu div.wp-menu-image:before,
    #adminmenu li a:focus div.wp-menu-image:before, #adminmenu li.opensub div.wp-menu-image:before, #adminmenu li:hover div.wp-menu-image:before {
        color: #555; }
    #adminmenu .current div.wp-menu-image:before, #adminmenu .wp-has-current-submenu div.wp-menu-image:before, #adminmenu a.current:hover div.wp-menu-image:before, #adminmenu a.wp-has-current-submenu:hover div.wp-menu-image:before, #adminmenu li.wp-has-current-submenu a:focus div.wp-menu-image:before, #adminmenu li.wp-has-current-submenu.opensub div.wp-menu-image:before, #adminmenu li.wp-has-current-submenu:hover div.wp-menu-image:before {
        color: white; }

    #adminmenu .wp-has-current-submenu .wp-submenu .wp-submenu-head,
    #adminmenu .wp-menu-arrow,
    #adminmenu .wp-menu-arrow div,
    #adminmenu li.current a.menu-top,
    #adminmenu li.wp-has-current-submenu a.wp-has-current-submenu,
    .folded #adminmenu li.current.menu-top,
    .folded #adminmenu li.wp-has-current-submenu {
        background: color(primary); }

    #wpadminbar .ab-top-menu>li.hover>.ab-item,
    #wpadminbar.nojq .quicklinks .ab-top-menu>li>.ab-item:focus,
    #wpadminbar:not(.mobile) .ab-top-menu>li:hover>.ab-item,
    #wpadminbar:not(.mobile) .ab-top-menu>li>.ab-item:focus,
    #wpadminbar .quicklinks .ab-sub-wrapper .menupop.hover>a,
    #wpadminbar .quicklinks .menupop ul li a:focus,
    #wpadminbar .quicklinks .menupop ul li a:focus strong,
    #wpadminbar .quicklinks .menupop ul li a:hover,
    #wpadminbar .quicklinks .menupop ul li a:hover strong,
    #wpadminbar .quicklinks .menupop.hover ul li a:focus,
    #wpadminbar .quicklinks .menupop.hover ul li a:hover,
    #wpadminbar .quicklinks .menupop.hover ul li div[tabindex]:focus,
    #wpadminbar .quicklinks .menupop.hover ul li div[tabindex]:hover,
    #wpadminbar li #adminbarsearch.adminbar-focused:before,
    #wpadminbar li .ab-item:focus .ab-icon:before,
    #wpadminbar li .ab-item:focus:before,
    #wpadminbar li a:focus .ab-icon:before,
    #wpadminbar li.hover .ab-icon:before,
    #wpadminbar li.hover .ab-item:before,
    #wpadminbar li:hover #adminbarsearch:before,
    #wpadminbar li:hover .ab-icon:before,
    #wpadminbar li:hover .ab-item:before,
    #wpadminbar.nojs .quicklinks .menupop:hover ul li a:focus,
    #wpadminbar.nojs .quicklinks .menupop:hover ul li a:hover,
    #wpadminbar:not(.mobile)>#wp-toolbar a:focus span.ab-label,
    #wpadminbar:not(.mobile)>#wp-toolbar li:hover span.ab-label,
    #wpadminbar>#wp-toolbar li.hover span.ab-label {
        color: color(primary); }


    #adminmenu .wp-submenu a:focus, #adminmenu .wp-submenu a:hover, #adminmenu a:hover, #adminmenu li.menu-top>a:focus,
    #adminmenu li.menu-top:hover, #adminmenu li.opensub>a.menu-top, #adminmenu li>a.menu-top:focus,
    #collapse-button:focus, #collapse-button:hover {
        color: color(primary); }


    &.wp-core-ui .button-primary {
        background: color(primary);
        border-color: darken(color(primary), 5%) darken(color(primary), 10%) darken(color(primary), 10%);
        box-shadow: 0 1px 0 darken(color(primary), 10%);
        text-shadow: 0 -1px 1px darken(color(primary), 10%), 1px 0 1px darken(color(primary), 10%), 0 1px 1px darken(color(primary), 10%), -1px 0 1px darken(color(primary), 10%);
        color: white;
        &:hover {
            background: darken(color(primary), 5%); } }

    &.wp-core-ui .button.button-primary.button-hero {
        box-shadow: 0 2px 0 darken(color(primary), 10%); } }

// ==================================================
// > ADMIN BAR
// ==================================================
#wp-admin-bar-wpfc-toolbar-parent > .ab-empty-item::before {
    content: "\f469" !important;
    top: 2px; }

// ==================================================
// > NINJA FORM
// ==================================================
.ninja-forms-no-nest {
    margin-left: 40px; }

.nf-field-wrap {
    &.fieldopentag {
        background: #eee linear-gradient(-135deg, #eee 0%, #eee 49.9%, white 50%, white 100%); }
    &.fieldclosetag {
        background: #eee linear-gradient(-135deg, white 0%, white 49.9%, #eee 50%, #eee 100%); }
    &.fieldopentag,
    &.fieldclosetag {
        margin-left: -40px;
        padding: 5px 20px;
        opacity: .25;
        &:hover {
            opacity: 1; }
        .nf-item-controls {
            top: 0px; } } }
.button.nf-insert-form {
    display: none !important; }


// ==================================================
// > ACF
// ==================================================
.acf-ui-datepicker #ui-datepicker-div {
    display: none; }

.acf-horizontal-repeater {
    .acf-table tbody {
        @include flex(row nowrap); }
    .acf-row:not(.acf-clone) {
        flex: 1;
        &,
        .acf-fields {
            @include flex(column nowrap); } }
    .acf-row {
        td {
            width: 100% !important;
            box-sizing: border-box; }
        .acf-field {
            width: 100%  !important;
            height: auto !important;
 } }            // min-height: 0 !important
    .acf-icon {
        &.-plus {
            left: -9px;
            top: 6px; }
        &.-minus {
            left: 50%; } }

    .acf-table > tbody > tr {
        & > td {
            border-width: 0 0 0 1px; }
        &:first-child > td {
            border-width: 0; } }

    .acf-repeater {
        padding-right: 10px;
        & > .acf-actions {
            @include absolute(top 50% right -10px);
            .button {
                overflow: hidden;
                text-shadow: none;
                @include font(primary);
                @include size(30px);
                position: relative;
                border-radius: 50%;
                &::before {
                    content: "+";
                    @include absolute(top 0 left 0);
                    @include size(100%);
                    @include font(20px white center); } } } } }


// ==================================================
// > EDITOR
// ==================================================
#tinymce {
    padding: 20px;
    *:first-child {
        margin-top: 0; } }

// ==================================================
// > LOGIN
// ==================================================
body.login {
    // background: img("") center center / cover no-repeat
    h1 a {
        background: img("logo.png") center center / contain no-repeat !important;
        background-size: contain;
        width: 200px;
        height: 200px;
        pointer-events: none;
        &::after {
            content: none; } }
    form {
        border-radius: 5px; }


    #wp-submit {
        display: block;
        @include size(100%, 48px);
        height: 48px !important;
        margin: 40px auto 0;
        padding: 0 20px;
        background: color(primary);
        box-shadow: none;
        border: 2px solid color(primary);
        border-radius: 5px;
        resize: none;
        @include font(16px 42px white bold uppercase);
        text-shadow: none;
        cursor: pointer;
        transition: all .4s ease;

        &:hover {
            background: white;
            color: color(primary); } }

    #nav, #backtoblog {
        text-align: center;
        a {
            @include font(14px white);
            &::after {
                bottom: -4px;
                background: white; } } } }


// ==================================================
// > USER FORM
// ==================================================
#profile-page {
    .user-rich-editing-wrap,
    .user-admin-color-wrap,
    .user-comment-shortcuts-wrap,
    // .show-admin-bar,
    .user-language-wrap,
    .user-url-wrap,
    .user-googleplus-wrap,
    .user-twitter-wrap,
    .user-facebook-wrap,
    .user-description-wrap,
    .user-profile-picture,
    .yoast-settings,
    #fieldset-billing,
    #fieldset-shipping,
    #qtranslate_highlight_enabled,
    h2,
    h3 {
        display: none; } }


#wpadminbar {
    position: fixed !important; }




